.article__item {
  display: flex;
  justify-content: center;
  margin-bottom: 4rem;
}
.article__item .img__wrapper {
  width: 505px;
  height: 330px;
  cursor: pointer;
}
.article__item .img__wrapper:hover {
  filter: contrast(125%);
}
.article__item .divider {
  width: 95px;
}
.article__item .article__content {
  width: 380px;
  align-self: center;
}
.article__item .article__content .article__title {
  font-size: 2.625rem;
  margin-bottom: 1.5rem;
}
.article__item .article__content .article__text {
  margin-bottom: 2rem;
  line-height: 150%;
  font-weight: 300;
}
.article__item .article__content .btn__counter {
  background: #E4C1AE;
  border: none;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  color: white;
  font-weight: 700;
  transition: 0.25s ease-in-out;
  cursor: pointer;
}
.article__item .article__content .btn__counter:hover {
  color: #121212;
  background: wheat;
}
.article__item:nth-child(even) {
  flex-direction: row-reverse;
  text-align: right;
}